<template>
  <div>
   
    <div class="text-center h1 mt-5" v-if="searchItems.length === 0">
      This page has no item
    </div>
    <div v-else class="d-flex ff flex-wrap m-2">
      <div class="item m-2" v-for="(item, i) in searchItems" :key="i">
        <div class="card p-1 m-2">
          <img :src="item.imgUrl" class="w-100 item-img" />
          <div class="item-name text-capitalize p-2">
            {{ item.name }}
          </div>
          <div class="item-price px-2">&#2547; {{ item.price }}</div>
          <div class="cart-added">
            <button
              @click="$store.commit('cartqyntityadded', item)"
              class="btn btn-primary w-100 my-2"
            >
              Add To Cart
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["items"]),
    searchItems() {
      return this.items.filter((e) => e.name.match(this.$route.params.search));
    },
  },
};
</script>
